@import url("https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap");

html {
  scroll-behavior: smooth;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.preloader-con {
  background-color: rgb(255, 255, 255);
  height: 100%;
  width: 100%;
  position: fixed;
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.login-con-bg {
  background-image: linear-gradient(to top, #012424 0%, #190d29 100%);
}

.validator-con {
  height: 91.5vh;
}

.home {
  font-family: "Ubuntu", sans-serif;
}

.hero-section {
  height: 100vh;
  position: relative;
  overflow: hidden;
  /* background-image: linear-gradient(to top, #012424 0%, #190d29 100%);

 */
}

.hero-section::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
  background-image: linear-gradient(20deg, #030244d5 0%, #170430d7);
}

.video-bg {
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  transform: translate(-50%, -50%);
  object-fit: cover;
  z-index: -1;
}
.right,
.left,
.logo,
.links {
  z-index: 2 !important;
}

.ani-box1 {
  background: linear-gradient(to top, #012424 0%, #190d29 100%);
}
.ani-box2 {
  background: linear-gradient(to top, #020124 0%, #0a0015 100%);
}
.ani-box3 {
  background: linear-gradient(to top, #100112 0%, #020035 100%);
}

.hero-section .nav {
  padding: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #fff;
}

.hero-section .nav .logo {
  width: 12rem;
}

.hero-section .nav .links ul {
  display: flex;
  list-style: none;
}

.hero-section .nav .links ul li a {
  color: #fff;
  text-decoration: none;
  transition: all 0.5s;
}

.hero-section .nav .links ul li a:hover {
  color: #ff4545;
}

.hero-section .nav .links ul li:not(:last-child) {
  padding-right: 1.5rem;
}

.hero-section .main {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  padding: 3.5rem 1.5rem;
}

.confitte-con{
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 2;
}
.hero-section .main .left .image {
  height: 25rem;
  width: 30rem;
}

.hero-section .main .right .countdown {
  display: flex;
  /* flex-wrap: wrap; */
  gap: 3rem;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-top: 2rem;
}

.hero-section .main .right .countdown div {
  background: linear-gradient(10deg, rgb(247, 247, 247), rgb(255, 255, 255));
  width: 4rem;
  height: 6rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 3rem;
  color: #000000;
  border-radius: 7px;
}

.hero-section .main .right .countdown div p:first-child {
  font-size: 2rem;
  font-weight: bold;
}

.hero-section .main .right .action-btn {
  text-align: center;
  margin-top: 2rem;
}

.hero-section .main .right .action-btn a {
  text-decoration: none;
  color: #ff4545;
}

.sponsor-section {
  background-color: white;
  overflow: hidden;
  padding: 0 0.5rem;
}

.sponsor-slider {
  display: flex;
  align-items: center;
  gap: 2rem;
  width: calc(1 * 100vw);
  animation: slide 20s linear infinite;
}

.sponsor-track {
  display: flex;
  align-items: center;
}

.sponsor-logo {
  width: 7rem;
  /*  filter: grayscale(100%); */
}

.w-box1 {
  z-index: 999 !important;
  position: absolute;
}
.w-box2 {
  z-index: 1 !important;
}

@keyframes slide {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}

/* Ticket Cards */

.tickets-con {
  background-image: url(../public/assets/wrt.jpg);
  position: relative;
}

.tickets-con::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
  background-image: linear-gradient(20deg, #00093ad7 0%, #3a0000a1);
}

.ticket-cards-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  padding: 50px;
}

.ticket-card {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 350px;
  height: 200px;
  padding: 10px;
  border: 2px dashed #333;
  background-color: #ffffff;
  position: relative;
  font-family: Arial, sans-serif;
  cursor: pointer;
  z-index: 2;
  transition: all 0.5s;
}
.book-btn-con {
  padding-bottom: 2em;
}
.book-btn {
  z-index: 2;
}
.ticket-card:hover {
  transform: scale(1.1);
}

.ticket-barcode-left,
.ticket-barcode-right {
  width: 20px;
  height: 100%;
  background-image: linear-gradient(to bottom, #000 50%, transparent 50%);
  background-size: 2px 6px;
}
.row--:hover{
    background-color: #dddddd;
   
}
.row---:hover{
  color: white !important;
}row---

.ticket-content {
  text-align: center;
  flex-grow: 1;
}

.ticket-title {
  font-size: 1.8rem;
  font-weight: bold;
  margin: 0;
}

.ticket-welcome {
  font-size: 0.9rem;
  margin: 5px 0;
}

.ticket-time,
.ticket-date {
  font-size: 0.8rem;
  margin: 2px 0;
}

.ticket-price {
  font-size: 1.2rem;
  font-weight: bold;
  margin-top: 10px;
}

.action-button-container {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

