  }
} */

.search-section {
  padding: 2rem 5rem;
  background: #eaeaea;
  color: #8a8a8a;
}

/* About Section */

/* Events */

.events-con {
  /* background-color: #999; */
  padding: 4rem 1.5rem;
}

.events-con .title {
  text-align: center;
  margin-bottom: 1rem;
  font-size: 1.5rem;
  font-weight: bold;
}

.events-con .text {
  text-align: center;
  letter-spacing: 0.2rem;
  font-size: 0.9rem;
}

.events {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-family: Arial, sans-serif;
}

.event-card {
  background: linear-gradient(to top, #012424 0%, #190d29 100%);
  padding: 15px;
  border-radius: 5px;
  margin-bottom: 10px;
  width: 300px;
}

.event-title {
  color: #fff;
  font-size: 1.2em;
  margin-bottom: 10px;
}

.event-detail {
  font-size: 0.9em;
  color: #f2f2f2;
  margin: 3px 0;
}

.stall {
  color: #000 ;
}
.clk-dwn{

    z-index: 999 !important ;
    position: absolute;
    left : 50%;
    transform: translate(-50%,50%);
}
/* Media Query Large */

@media (max-width: 900px) {

  .hero-section .nav {
    padding: 1.5rem 0;
  }

  .hero-section {
    height: 100%;
  }

  .hero-section .nav .links {
    display: none;
  }

  .hero-section .main {
    padding: 0 1.5rem;
    flex-direction: column-reverse;
  }

  .hero-section .main .left .image {
    height: 25rem;
    width: 25rem;
  }

  .hero-section .main .right .heading {
    text-align: center;
  }

  .hero-section {
    padding-bottom: 1.5rem;
  }





}

@media (max-width: 768px) {
  

  .ticket-cards-container {
    flex-direction: column;
    align-items: center;
    padding-left: 1em;
    padding-right: 1em;
  }

  .sponsor-slider {

    width: calc(3.5 * 100vw);
    animation: slide 10s linear infinite;
  }

  .ticket-card {
    width: 80%;
  }

  .search-section {
    padding: 1rem 1rem;
  
  }
  
.wtr-con{
  height: 70vh !important;
}

}

/* Media Query Small */

@media (max-width: 586px) {
  #popgetticket .css-10d30g3-MuiPaper-root-MuiDialog-paper{
    margin: 9px !important;
  }
  .hero-section {
    padding-bottom: 1.5rem;
  }

  .hero-section .main .left .image {
    height: 25rem;
    width: 19rem;
  }

  .hero-section .main .right .countdown {
    gap: 0;
  }
  .hero-section .main .right .countdown div {
    width: 1.5rem;
    padding: 1rem 2.5rem;
    border-radius: 0;
  }
  .ticket-card {
    width: 100%;
  }
  .search-section {
    padding: 2rem;
  }

    .v-con-x{

       width: 95%;
       margin: 3px;
    }

    .h-text-inner{
       display: none;
    }
}

@media (max-width: 320px) {

  .hero-section .main .right .countdown div {
    padding: 1rem 2.3rem;
   
  }

}
