.container {
  position: relative;
  overflow: hidden; /* Hides any overflow from animations */
  width: 100%; /* Ensures container takes the full viewport width */
  height: 100%; /* Ensures container takes the full viewport height */
  z-index: 3;
}

.container > div {
  cursor: pointer;
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-weight: 800;
  font-size: 4em;
  will-change: transform, opacity;
  text-shadow: 0px 2px 40px #00000020, 0px 2px 5px #00000030;
  -webkit-user-select: none;
  user-select: none;
  overflow: hidden; /* Prevents overflow in individual pages as well */
}

 #textp-m { 

  font-size: 10px !important;

}

@media (max-width: 768px) {
  



  .container > div {
   
    font-weight: 800;
    font-size: 10vw;
   
  }
  
}